@tablet-width: 660px;
@desktop-width: 960px;
@mobile-width-only: 659px;

body {
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #ffffff;
  background-repeat: no-repeat;
  margin: 0;
}

.page__body {
  min-height: 100%;
  background-image: url("../img/town.png");
  background-repeat: no-repeat;
  margin: 0 auto;
}

.site-list {
  list-style: none;
}

.visually-hidden {
  visibility: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.button {
  width: 276px;
  height: 59px;
  background-color: #2aab6d;
  border: 2px solid #2aab6d;
  border-radius: 1px;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
}

.slider__toggles {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 19px;
  text-align: center;
  padding: 0;
  margin-top: 8px;
  /*  margin-bottom: 60px;*/
}

.slider__toggle {
  padding: 0;
  width: 6px;
  height: 6px;
  background: fade(#283645, 30%);
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.slider__toggle--current {
  position: relative;
}

.slider__toggle--current::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;

  width: 6px;
  height: 6px;
  background: #ffffff;
  border: 3px solid #283645;
  border-radius: 50%;
  cursor: pointer;
}

.slider__toggle,
.slider__toggle--current {
  outline: none;
  cursor: pointer;
}

.slider__toggle:hover {
  background: fade(#283645, 60%);
}

.slider__toggle:active {
  background: fade(#283645, 10%);
}

@media (min-width: @desktop-width) {
  .page__body {
    width: 100%;
    margin: 0 auto;
  }
}
